
import { defineComponent, inject } from 'vue';
import { AccordionType } from '@/types/Accordion';
import BaseIconAsync from '@/components/base/BaseIconAsync.vue';
import BaseIcon from '@/components/base/BaseIcon.vue';
import IconChevronDown from '@/components/icons/IconChevronDown.vue';
import IconChevronUp from '@/components/icons/IconChevronUp.vue';

export default defineComponent({
  components: { BaseIconAsync, BaseIcon, IconChevronDown, IconChevronUp },
  name: 'AccordionItem',
  // setup() method is needed for injection, in order to make it work with typescript
  // inject: ['Accordion'],
  setup() {
    const Accordion = inject('Accordion') as AccordionType;

    return {
      Accordion,
    };
  },
  props: {
    expandOnCreated: {
      // open this accordion-item once it's created
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: '',
    },
    icon: {
      type: String,
    },
  },
  data() {
    return {
      index: -1,
    };
  },
  computed: {
    visible(): boolean {
      if (this.Accordion.allowMultiple) {
        return (this.Accordion.active as number[]).includes(this.index);
      }
      return this.index === this.Accordion.active;
    },
    headingLevel() {
      return this.Accordion.headingLevel;
    },
    idFormatted(): { header: string; content: string } {
      return {
        header: `__acc-h--${this.Accordion.baseId}-${this.index}`,
        content: `__acc-c--${this.Accordion.baseId}-${this.index}`,
      };
    },
  },
  methods: {
    toggle() {
      if (this.Accordion.allowMultiple) {
        let list = this.Accordion.active as number[];
        if (this.visible) {
          list.splice(list.indexOf(this.index), 1);
        } else {
          list.push(this.index);
        }
      } else {
        this.Accordion.active = this.visible ? null : this.index;
      }
    },
    start(el: HTMLElement) {
      // animation
      el.style.height = `${el.scrollHeight}px`;
    },
    end(el: HTMLElement) {
      // animation
      el.style.height = '';
    },
  },
  created() {
    this.index = this.Accordion.count++;
    if (this.expandOnCreated || window.location.hash === '#' + this.idFormatted.header) {
      this.toggle();
    }
  },
});
